.Card {
    background-size: cover;
    background-color: chocolate;
    height: 700px;
    width: 500px;
    display: grid;
    grid-template-columns:2fr 1fr 1fr 1fr 1fr 1fr 1fr 1fr 1fr 1fr;
    grid-gap: 1px;
    /*align-items: center;*/
    /*justify-items: center;*/

}

.Score {
  background-color: antiquewhite;
}

.Center {
    text-align: center;
    position: relative;
    top: 50%;
    transform: translate(0 , -50%);

}

.Input {
    width: 30px;
    height: 13px;
    text-align: center;
    outline: none;
}